
nav{-ms-flex:0 0 200px;flex:0 0 200px
}
@media all and (max-width: 700px){
nav{-ms-flex:none;flex:none
}
}
.nav__list{list-style:none;margin:0;padding:0;font-size:14px;display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;text-align:left
}
.nav__item{width:100%
}
.nav__link{display:inline-block;overflow:hidden;text-decoration:none;padding:0;text-transform:uppercase;font-size:40px;transition:all .2s ease-in-out;line-height:29px;height:23px;border-bottom:1px solid rgba(204,204,204,0.52);font-weight:bold;color:#2c3e50
}
.nav__link:hover{color:#05b7ee
}
.nav__link.nav__link--active{border-color:#05b7ee;color:#05b7ee
}



