
.footer{border-top:2px solid #05b7ee;background:#fefefe;padding:20px
}
.footer__list{max-width:650px;margin:0 auto;padding:0;display:-ms-flexbox;display:flex;-ms-flex-direction:row;flex-direction:row;list-style:none
}
@media all and (max-width: 500px){
.footer__list{-ms-flex-direction:column;flex-direction:column;font-size:12px
}
.footer__item{padding-bottom:3px
}
}
.footer__item{width:100%;text-align:center
}
.footer__item a{color:#05b7ee
}
.footer__item a:hover{text-decoration:underline
}



